<template>
  <main id="app">
    <dice-header />
    <dice-game :orders="apporders"></dice-game>
    <!-- <dice-orders /> -->
    <dice-orders :orders="apporders" :yourorders="yourorders"></dice-orders>
  </main>
</template>

<script>
  /* global scatter */
  const task = new Promise(r => {
    document.addEventListener('scatterLoaded', r);
  });

  export default {
    mounted() {
      task.then(() => {
        if (!scatter.identity) return;
        const account = scatter.identity.accounts.find(account => account.blockchain === 'eos');
        if (!account) return;
        this.$store.commit('UPDATE_ACCOUNT', account);
      });
      this.fetchOrders();
    },

    components: {
      diceHeader: require('@/components/header').default,
      diceGame: require('@/components/game').default,
      diceOrders: require('@/components/orders').default
    },

    data() {
      return {
        // apporder:{},
        apporders: [],
        yourorders: [],
      }
    },

    methods: {
      fetchOrders() {
        // return;

        Pusher.logToConsole = true;

        var pusher = new Pusher('df97ed45f323bb2d64d5', {
          cluster: 'ap1',
          forceTLS: true
        });

        var channel = pusher.subscribe('my-channel');
        channel.bind('my-event', (data)=>{
          // alert(data);

          var edge = 200;
          if(data.referral !== 'eoslotcasino'){
            edge = 150;
          }

          var payout = '0.0000 EOS';
          if(data.roll_under > data.random_roll){
            payout = data.bet_amt * ((10000-edge)*100/(data.roll_under - 1)) /100000000;
            payout = (new Number(payout).toFixed(4)).toString() + " EOS";;
          }

          var bet_amt = (new Number(data.bet_amt / 10000).toFixed(4)).toString() + " EOS";

          data.bet_amt = bet_amt;
          data.payout = payout;
          this.apporders.splice(0, 0, data);

          if(!this.$store.state.account){
            if(this.$store.state.account.name == data.bettor){
              this.yourorders.splice(0,0,data);
            }
          }
          // this.apporders = this.apporders;
        });

        // channel.bind('my-event', function(data) {
        //   alert(data);
        //   this.apporder = 'cccddd';
        //   // var row = "<tr>" +
        //   //                          "<td style='text-align: center;'>" +  new Date(data.bet_time+'Z').toLocaleTimeString() + "</td>" +
        //   //                          "<td style='text-align: center;'>" +  data.bettor      + "</td>" +
        //   //                          "<td style='text-align: center;'>" +  data.roll_under      + "</td>" +
        //   //                          "<td style='text-align: center;'>" +  data.bet_amt      + "</td>" +
        //   //                          "<td style='text-align: center;'>" +  data.rand      + "</td>" +
        //   //                          "<td style='text-align: center;'>" +  (data.payout !== '0.0000 SYS' && data.payout || '' )    + "</td>" +
        //   //                     "</tr>";

        //   //                   // alert(row);
        //   // $('.orders-table tbody:last').prepend(row);

        //   // this.diceOrders.orders.push(data);
        // }); 


      },
    }
  };
</script>

